.onsite_preview_inner {
       padding-bottom: calc(100vh - 312px);
       overflow: hidden;
       position: relative;
}

.t_modal {
       position: absolute;
       transform: scale(0.81);
       width: 124%;
       height: 124%;
       left: -12%;
       right: -12%;
       top: -12%;
       bottom: -12%;
       z-index: 1;
       overflow-y: auto;
}

#subtree-container .t_modal{
       z-index: 9999;
}

.tb_onsite_upload_btn_wrap {
       padding: 10px
}

.tb_onsite_upload_btn,
.tb_onsite_upload_btn_wrap {
       align-items: center;
       display: flex;
       justify-content: center
}

.tb_onsite_upload_btn {
       flex-direction: column;
       max-width: 350px
}

.tb_onsite_btn {
       background-color: #000;
       border-radius: 3px;
       color: #fff;
       cursor: pointer;
       font-weight: 500;
       padding: 6px
}

.tb_onsite_btn,
.tb_onsite_qr_btn_wrap {
       align-items: stretch;
       display: flex;
       justify-content: center
}

.tb_onsite_qr_code {
       align-items: center;
       background: #fff;
       display: flex;
       justify-content: center;
       object-fit: cover;
       padding: 5px
}

.tb_onsite_upload_btn .btn-scan canvas {
       height: 100%;
       object-fit: cover;
       width: 100%
}

.tb_onsite_btn_txt {
       align-items: center;
       display: flex;
       font-size: 14px;
       font-weight: 400;
       padding: 0 10px
}