.t_modal_container {
    min-height: calc(100% - 40px);
    max-width: 700px;
    width: 100%;
    margin: 20px auto;
    padding: 0 15px;
    align-items: center;
    display: flex;
}


.t_modal_content {
    background-clip: padding-box;
    background-color: #fff;
    color: #000;
    display: flex;
    flex-direction: column;
    pointer-events: auto;
    position: relative;
    width: 100%;
    border-radius: 5px;
    outline: 0px;
    padding: 20px;
    min-height: 500px;
}


.t_m_title {
    font-size: 24px;
    margin-bottom: 20px;
    margin-top: 0;
    font-weight: 600;
}

.t_label_ {
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 8px;
    color: #2b2b2b;
}

.t_t_danger {
    display: inline;
    color: red;
    font-size: 14px;
}

.t_from_input,
.t_from_textarea {
    border: 1px solid #BABFC3;
    border-radius: 3px;
    box-shadow: none;
    font-family: Inter, sans-serif;
    font-size: 14px;
    outline: none;
    padding: 10px;
    width: 100%;
    color: #2b2b2b;
}

.t_from_textarea {
    min-height: 120px;
    resize: vertical;
}

.t_from_input:focus,
.t_from_textarea:focus {
    border-color: #2b2b2b;
}

.t_f_group {
    margin-bottom: 15px;
}

.t_from_check {
    appearance: none;
    background-color: #fff;
    background-image: var(--bs-form-check-bg-image);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: contain;
    border: 1.5px solid #2b2b2b;
    flex-shrink: 0;
    margin-right: 6px;
    height: 1.3em;
    margin-top: 0;
    border-radius: 2px;
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact;
    width: 1.3em;
}

.t_m_upload {
    height: 76px;
    width: 76px;
    border: 1px solid #BABFC3;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #BABFC3;
    position: relative;
    margin-right: 10px;
    margin-bottom: 10px;
    font-size: 12px;
    text-align: center;
}
.t_m_upload:hover {
    border: 1px solid #545454;
    color: #545454;
}

.t_m_up_img_wrap {
    height: 76px;
    width: 76px;
    position: relative;
    margin-right: 10px;
    margin-bottom: 10px;
}

.t_m_up_img_in{
    position: relative;
    height: 100%;
    width: 100%;
}

.t_m_up_img{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
    border: 1px solid #BABFC3;
    border-radius: 3px;
}

.t_m_vi_ico {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    color: #fff;
    filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, .3));
}

.t_m_remove_btn{
    position: absolute;
    top: -5px;
    right: -5px;
    background-color: #ff4051;
    height: 18px;
    width: 18px;
    font-size: 10px;
    text-align: center;
    border-radius: 50%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.t_m_upload .tb__icon {
    font-size: 28px;
}

.t_m_upload .tb__icon.tb-plus_icon{
    transform: rotate(45deg);
    font-size: 22px;
}

.t_from_check:checked {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'%3E%3Cpath fill='none' stroke='%23fff' stroke-linecap='round' stroke-linejoin='round' stroke-width='3' d='m6 10 3 3 6-6'/%3E%3C/svg%3E");
    background-color: #2b2b2b;
}

.t_f_check_inline {
    display: flex;
    align-items: start;
}

.t_info_{
    font-size: 10px;
    color: #BABFC3;
    margin-bottom: 5px;
}

.t_f_label_wrap{
    display: flex;
    flex-direction: column;
    justify-content: start;
    text-align: start;
    margin-bottom: 10px;
}

.t_f_label_wrap .t_check_label_{
    margin-bottom: 5px;
}

.t_f_label_wrap .t_tc_link{
    color: #1154cc;
}

.t_check_label_ {
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 8px;
    color: #2b2b2b;
    display: inline-block;
    cursor: pointer;
    user-select: none;
}

.t_row_ {
    display: flex;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
}

.t_col_ {
    flex: 1 0 100%;
    padding: 0 10px;
}

.t_tc_link {
    font-weight: 500;
    text-decoration: underline;
    color: #2b2b2b;
    font-size: 12px;
}

.t_f_p_btn {
    background-color: #000;
    border: 0;
    border-radius: 3px;
    box-shadow: none;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.4;
    min-width: 100px;
    opacity: .8;
    outline: none;
    padding: 10px 12px;
    text-align: center;
    display: inline-block;
}

.t_f_p_btn.t_f_btn_spinner {
    padding-right: 40px;
    opacity: 0.9;
    box-shadow: none;
    pointer-events: none;
    position: relative;
}

.t_f_btn_spinner:after {
    content: "";
    position: absolute;
    width: 20px;
    right: 12px;
    top: calc(50% - 11px);
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 50%;
    border: 1.5px solid #ffffff;
    border-right: 2px solid transparent;
    animation: tb_onsite_animation-spinner 0.5s linear infinite;
}

.t_m_close_btn {
    color: #000;
    font-size: 14px;
    line-height: 16px;
    opacity: .5;
    position: absolute;
    right: 10px;
    top: 10px;
    z-index: 1;
    cursor: pointer;
    padding: 10px;
}

.t_m_close_btn:hover {
    opacity: .7;
}

.t_rate_wrap {
    display: flex;
    align-items: center;
}

.t_star_ico {
    padding: 8px;
    text-align: center;
    background-color: #D9D9D9;
    margin-right: 8px;
    font-size: 20px;
    color: #fff;
    border-radius: 3px;
    cursor: pointer;
}

.t_star_ico_wrap.t_active_ .t_star_ico,
.t_star_ico_wrap.t_h_active_ .t_star_ico {
    background-color: #fbbc04;
}


.t_m_propgress{
    display: flex;
    align-items: center;
    justify-content: center;
}

.t_f_error{
    font-size: 11px;
    color: #ff4051;
    margin: 2px 0;
}

.t_f_invalid {
    border: 1px solid #ff4051;
}

.t_f_invalid + .t_f_error{
    display: block;
}

.t_f_upload{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.t_m_upload .uppy-FileInput-btn{
    background-color: transparent;
    font-size: 0;
    border: none;
    height: 100%;
    width: 100%;
    cursor: pointer;
}

.t_m_upload .uppy-Container{
    position: absolute;
    width: 76px;
    height: 76px;
    top: 0;
    left: 0;
    z-index: 1;
}

.t_m_upload .uppy-FileInput-container,
.t_m_upload .uppy-Container,
.t_m_upload .uppy-Root{
    height: 100%;
    width: 100%;
}

.t_s_wrap{
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 100px 0;
}

.t_s_title{
    font-size: 22px;
    text-align: center;
    font-weight: 600;
    margin-bottom: 12px;
}

.t_s_desc{
    text-align: center;
    color: #545454;
    font-size: 14px;
}

.t_s_icon_ {
    margin: 15px auto;
    width: 70px;
    height: auto;
}

.t_f_close_wrap{
    margin-top: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media (min-width: 476px) {
    .t_modal_content{
        padding: 30px;
    }
}

@media (min-width: 991px) {
    .t_col_ {
        flex: 1 0 0%;
    }
}
.t_overlay_ {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99998;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .7);
}